import React from "react";
import { useState } from "react";
import {
	Container,
	Typography,
	Select,
	MenuItem,
	FormControl,
	FormHelperText,
	InputLabel,
	Slider,
	Checkbox,
	TextField,
	Button,
	Dialog,
	DialogActions,
	DialogContent,
	DialogTitle,
	FormControlLabel,
} from "@mui/material";

function MokkivuokrausApp() {
	const [mokki, setMokki] = useState("");
	const [paiva, setPaiva] = useState("");
	const [varaaja, setVaraaja] = useState("");
	const [open, setOpen] = useState(false);
	const [vuorokaudet, setVuorokaudet] = useState(1);
	const [hinta, setHinta] = useState("");
	const [siivous, setSiivous] = useState(false);

	const valitseMokki = (event) => {
		setHinta(event.target.value);
		setMokki(event.currentTarget.id);
	};

	const varaa = () => {
		setOpen(true);
	};
	const suljeVaraus = () => {
		setOpen(false);
	};
	const tallennaVaraus = () => {
		setOpen(false);
		alert("Varaus tallennettu");
	};
	const handleChange = (event, newValue) => {
		event.preventDefault();
		setVuorokaudet(newValue);
	};

	return (
		<Container maxWidth="sm">
			<br />
			<Typography variant="h4">Lomamkkivuokraus</Typography>
			<br />
			<FormControl>
				<InputLabel>Mkki</InputLabel>
				<Select value={hinta} onChange={valitseMokki}>
					<MenuItem id="Mkker" value={100}>
						Mkker: 100 /vrk
					</MenuItem>
					<MenuItem id="Hkkeli" value={80}>
						Hkkeli: 80 /vrk
					</MenuItem>
					<MenuItem id="Tlli" value={150}>
						Tlli: 150 /vrk
					</MenuItem>
					<MenuItem id="Rttel" value={75}>
						Rttel: 75 /vrk
					</MenuItem>
				</Select>
				<FormHelperText>Valitse haluamasi mkki</FormHelperText>
			</FormControl>
			<br />
			<br />
			<Typography display="inline">
				Vuokra-aika ({vuorokaudet} vrk) :
			</Typography>
			<Slider
				value={vuorokaudet}
				onChange={handleChange}
				valueLabelDisplay="auto"
				step={1}
				marks
				min={1}
				max={14}
			/>
			<FormControlLabel
				control={
					<Checkbox
						checked={siivous}
						onChange={(event) => {
							event.target.checked
								? setSiivous(true)
								: setSiivous(false);
						}}
						color="primary"
					/>
				}
				label="Loppusiivouspalvelu"
			></FormControlLabel>
			<br />
			<Typography>
				Varauksen kokonaissumma:{" "}
				{hinta * vuorokaudet + (siivous ? 100 : 0)} 
			</Typography>
			<br />

			<TextField
				InputLabelProps={{ shrink: true }}
				label="Varaajan nimi"
				placeholder="Etunimi Sukunimi"
				variant="filled"
				onChange={(event) => {
					setVaraaja(event.target.value);
				}}
				type="text"
			></TextField>
			<br />
			<br />

			<TextField
				defaultValue={paiva}
				InputLabelProps={{ shrink: true }}
				label="Saapumispiv"
				variant="filled"
				onChange={(event) => {
					setPaiva(event.target.value);
				}}
				type="date"
			></TextField>
			<br />
			<br />
			<Button onClick={varaa} variant="contained" color="primary">
				Varaa mkki
			</Button>
			<Dialog
				onClose={suljeVaraus}
				aria-labelledby="customized-dialog-title"
				open={open}
			>
				<DialogTitle id="customized-dialog-title" onClose={suljeVaraus}>
					Varauksen yhteenveto
				</DialogTitle>
				<DialogContent dividers>
					<Typography gutterBottom>
						Varaamasi mkki: {mokki}{" "}
					</Typography>
					<Typography gutterBottom>
						Vuokra-aika: {vuorokaudet} vuorokautta
					</Typography>
					<Typography gutterBottom>
						Loppusiivous: {siivous ? "Kyll" : "Ei"}
					</Typography>

					<Typography gutterBottom>
						Loppusumma: {hinta * vuorokaudet + (siivous ? 100 : 0)}{" "}
						
					</Typography>
					<Typography gutterBottom>
						Saapumispivmr: {paiva}
					</Typography>
					<Typography gutterBottom>
						Varaajan nimi: {varaaja}{" "}
					</Typography>
				</DialogContent>
				<DialogActions>
					<Button
						autoFocus
						variant="contained"
						onClick={suljeVaraus}
						color="primary"
					>
						Peruuta
					</Button>
					<Button
						autoFocus
						variant="contained"
						onClick={tallennaVaraus}
						color="primary"
					>
						Tallenna varaus
					</Button>
				</DialogActions>
			</Dialog>
		</Container>
	);
}

export default MokkivuokrausApp;
